@font-face {
  font-family: "RobotoCondesed";
  src: url("../fonts/robotocondensed.woff2") format("woff2"), url("../fonts/Robotocondensed.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "RobotoCondesed";
  src: url("../fonts/robotocondensedbold.woff2") format("woff2"), url("../fonts/Robotocondensedbold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  src: url("../fonts/roboto.woff2") format("woff2"), url("../fonts/Roboto.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "PT Mono";
  src: url("../fonts/ptmono.woff2") format("woff2"), url("../fonts/ptmono.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
html {
  height: 100%;
}

body {
  font-family: "PT Mono", "Arial", sans-serif;
  margin: 0 auto;
  height: 100%;
}

.body__wrapper {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.main {
  flex: 1 0 auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 2;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.8);
}

.overlay--hidden {
  display: none;
}

.button {
  font-family: "RobotoCondesed", sans-serif;
  color: #ffffff;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.05em;
  border-radius: 5px;
  border: none;
  background: none;
}

.button--header {
  width: 180px;
  height: 45px;
  border: 1px solid #ffffff;
  box-sizing: border-box;
}
.button--header:hover {
  background: #ffffff;
  color: #1b2d37;
}
@media (max-width: 1023px) {
  .button--header {
    display: none;
  }
}

.button--promo {
  width: 407px;
  height: 55px;
  text-decoration: none;
  background: #00b2ff;
  margin-left: 98px;
  font-size: 16px;
  line-height: 19px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.button--promo:hover {
  background: #0098ac;
}
@media (max-width: 1023px) {
  .button--promo {
    width: 379px;
    margin-left: 49px;
  }
}
@media (max-width: 767px) {
  .button--promo {
    width: 287px;
    margin-left: 3px;
  }
}

@media (max-width: 767px) {
  .button--promo .promo__tablet {
    display: none;
  }
}

.button--promo .promo__mobile {
  display: none;
}
@media (max-width: 767px) {
  .button--promo .promo__mobile {
    display: block;
  }
}

.button--about {
  width: 180px;
  height: 45px;
  color: #00b2ff;
  border: 1px solid #00b2ff;
  box-sizing: border-box;
}
.button--about:hover {
  color: #ffffff;
  background: #00b2ff;
}
@media (max-width: 1023px) {
  .button--about {
    margin-left: 3px;
  }
}

.button--product {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 63px;
  height: 63px;
  background-color: #00b2ff;
  position: absolute;
  border-radius: 0;
  bottom: 0;
  right: 0;
}

.button--question {
  grid-row: 3;
  width: 270px;
  height: 45px;
  color: #ffffff;
  background: none;
  border: 1px solid #ffffff;
  box-sizing: border-box;
}
.button--question:hover {
  background: #ffffff;
  color: #00b2ff;
}
@media (max-width: 1023px) {
  .button--question {
    width: 280px;
    height: 47px;
  }
}
@media (max-width: 767px) {
  .button--question {
    width: 100%;
    grid-row: 4;
  }
}

.button--sections {
  text-align: left;
  font-family: "RobotoCondesed", sans-serif;
  font-weight: 400;
  font-size: 15px;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #1b2d37;
  margin-left: 3px;
  padding: 0;
}
@media (max-width: 767px) {
  .button--sections {
    width: 100%;
    background: url("../img/Union.svg") no-repeat;
    background-position: right;
  }
}

@media (max-width: 767px) {
  .button--close {
    background: url("../img/minus.svg") no-repeat;
    background-position: right;
  }
}

.button--no-js {
  background: none;
}

.button--modal {
  width: 100%;
  height: 47px;
  color: #ffffff;
  background: none;
  border: 1px solid #ffffff;
  box-sizing: border-box;
  margin-bottom: 26px;
}
.button--modal:hover {
  background: #ffffff;
  color: #00b2ff;
}

.button--modal-close {
  position: absolute;
  top: 19px;
  right: 14px;
}

.header {
  max-width: 1460px;
  position: absolute;
  display: flex;
  padding: 33px 25px 0;
  box-sizing: border-box;
  width: 100%;
  align-items: center;
  top: 43px;
  left: 50%;
  transform: translate(-50%, -50%);
}
@media (max-width: 1023px) {
  .header {
    padding-top: 7px;
  }
}
@media (max-width: 767px) {
  .header {
    top: 33px;
    padding-left: 17px;
    padding-right: 16px;
  }
}

.header__logo {
  display: flex;
  text-align: left;
}

@media (max-width: 767px) {
  .header__logo img {
    width: 38px;
    height: 38px;
  }
}

.header__name {
  padding: 6px 0 0 16px;
}
@media (max-width: 767px) {
  .header__name {
    display: none;
  }
}

.header h2 {
  font-family: "RobotoCondesed", sans-serif;
  font-weight: 700;
  font-size: 25px;
  line-height: 29px;
  color: #ffffff;
  margin: 0;
}

.header p {
  font-family: "Roboto", sans-serif;
  font-weight: 500;
  font-size: 12px;
  line-height: 14px;
  color: #788891;
  margin: 0;
}

.header__nav {
  margin-left: auto;
}

.header__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
}

.header__list li {
  margin-right: 122px;
}
@media (max-width: 1023px) {
  .header__list li {
    margin-right: 58px;
  }
}

.header__list li:last-child {
  margin-right: 49px;
}
@media (max-width: 1023px) {
  .header__list li:last-child {
    margin-right: 0;
  }
}

.header__list li a {
  text-decoration: none;
  color: #ffffff;
  position: relative;
}

@media (max-width: 1023px) {
  .header__list li span {
    display: none;
  }
}

.header__mail::before {
  content: "";
  display: block;
  position: absolute;
  width: 19px;
  height: 14px;
  background: url("../img/mail-desktop.svg") no-repeat;
  left: -39px;
  top: 2px;
}
@media (max-width: 1023px) {
  .header__mail::before {
    width: 25px;
    height: 19px;
    left: -25px;
    top: -11px;
    background-size: contain;
  }
}
@media (max-width: 767px) {
  .header__mail::before {
    left: -14px;
  }
}

.header__phone::before {
  content: "";
  display: block;
  position: absolute;
  width: 18px;
  height: 18px;
  background: url("../img/phone-desktop.svg") no-repeat;
  left: -34px;
  top: 0;
}
@media (max-width: 1023px) {
  .header__phone::before {
    width: 24px;
    height: 24px;
    background-size: contain;
    left: -24px;
    top: -15px;
  }
}

.promo {
  min-height: 878px;
  background: #1b2d37 url("../img/promo.png") no-repeat;
  background-size: cover;
  display: flex;
  padding-left: 250px;
  padding-top: 258px;
  box-sizing: border-box;
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .promo {
    background: #1b2d37 url("../img/promo@2x.png") no-repeat;
    background-size: cover;
  }
}
@media (max-width: 1023px) {
  .promo {
    min-height: 686px;
    padding-top: 131px;
    padding-left: 89px;
    background: #1b2d37 url("../img/promo-tablet.png") no-repeat;
    background-size: cover;
  }
}
@media (max-width: 1023px) and (min-resolution: 144dpi), (max-width: 1023px) and (min-resolution: 1.5dppx) {
  .promo {
    background: url("../img/promo-tablet@2x.png") no-repeat;
    background-size: cover;
  }
}
@media (max-width: 767px) {
  .promo {
    min-height: 647px;
    padding: 228px 15px 0;
    background: #1b2d37 url("../img/promo-mobile.png") no-repeat;
    background-size: cover;
  }
}
@media (max-width: 767px) and (min-resolution: 144dpi), (max-width: 767px) and (min-resolution: 1.5dppx) {
  .promo {
    background: url("../img/promo-mobile@2x.png") no-repeat;
    background-size: cover;
  }
}

.promo__image {
  margin-left: 73px;
  position: absolute;
  right: 177px;
  z-index: 0;
}
@media (max-width: 1023px) {
  .promo__image {
    right: 0;
    top: 113px;
  }
}
@media (max-width: 767px) {
  .promo__image {
    top: 92px;
  }
}

@media (max-width: 1023px) {
  .promo__image img {
    width: 260px;
    height: 340px;
  }
}
@media (max-width: 767px) {
  .promo__image img {
    width: 293px;
    height: 220px;
  }
}

.promo__text {
  z-index: 1;
}

.promo__text h2 {
  font-family: "RobotoCondesed", sans-serif;
  font-weight: 700;
  font-size: 80px;
  line-height: 94px;
  letter-spacing: 0.07em;
  text-transform: uppercase;
  color: #ffffff;
  margin-top: 58px;
  margin-bottom: 29px;
}
@media (max-width: 1023px) {
  .promo__text h2 {
    font-size: 60px;
