  }
}
@media (max-width: 767px) {
  .promo__text h2 {
    width: 277px;
    font-size: 40px;
    line-height: 47px;
    letter-spacing: 0.07em;
    margin-bottom: 27px;
  }
}

.promo__text p {
  max-width: 683px;
  font-family: "PT Mono", sans-serif;
  font-size: 22px;
  line-height: 34px;
  color: #ffffff;
  padding-left: 98px;
  margin-bottom: 40px;
  position: relative;
}
@media (max-width: 1023px) {
  .promo__text p {
    max-width: 356px;
    font-size: 18px;
    line-height: 28px;
    padding-left: 49px;
    margin-bottom: 54px;
  }
}
@media (max-width: 767px) {
  .promo__text p {
    line-height: 156.8%;
    font-size: 16px;
    padding-left: 51px;
    margin-bottom: 40px;
  }
