.promo__text p::before {
  content: "";
  display: block;
  position: absolute;
  width: 66px;
  height: 2px;
  background: #ffffff;
  left: 4px;
  top: 14px;
}
@media (max-width: 1023px) {
  .promo__text p::before {
    width: 33px;
    left: 2px;
    top: 15px;
  }
}

.about {
  padding: 112px 0 110px 0;
}
@media (max-width: 1023px) {
  .about {
    padding: 54px 0 83px 0;
  }
}
