.modal {
  display: none;
  width: 490px;
  min-height: 605px;
  background: #00b2ff;
  padding: 30px 39px 36px;
  box-sizing: border-box;
  position: absolute;
  z-index: 2;
  top: 136px;
  left: 50%;
  transform: translate(-50%, 0);
  overflow-y: hidden;
}
@media (max-width: 1023px) {
  .modal {
    left: 0;
    top: 0;
    transform: translate(0, 0);
    width: 100%;
    overflow: hidden;
    min-height: 687px;
  }
}
@media (max-width: 767px) {
  .modal {
    min-height: 648px;
  }
}

.modal h2 {
  font-family: "RobotoCondesed", sans-serif;
  font-weight: 400;
  font-size: 28px;
  line-height: 140.62%;
  color: #ffffff;
  margin: 0;
}

.modal p {
  width: 380px;
  font-family: "PT Mono", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  color: #ffffff;
  margin: 0;
  padding-top: 12px;
  padding-bottom: 31px;
}

.modal__input input {
  width: 100%;
  border: none;
  height: 52px;
  background: rgba(255, 255, 255, 0.1);
  margin-bottom: 30px;
  padding: 15px 24px;
  box-sizing: border-box;
}

.modal__form input input::placeholder {
  font-family: "PT Mono", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  color: #ffffff;
}

.modal__form textarea {
  width: 100%;
  height: 104px;
  background: rgba(255, 255, 255, 0.1);
  border: none;
  padding: 13px 24px;
  box-sizing: border-box;
  margin-bottom: 26px;
}

.modal__form textarea::placeholder {
  font-family: "PT Mono", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  color: #ffffff;
}

.modal__checkbox {
  display: inherit;
  font-family: "PT Mono", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  color: #ffffff;
  opacity: 0.8;
  margin-left: 49px;
  margin-top: 8px;
  margin-right: 19px;
  position: relative;
}

.modal__checkbox::before {
  content: "";
  display: block;
  position: absolute;
  width: 16px;
  height: 16px;
  border: 1px solid #ffffff;
  box-sizing: border-box;
  top: 1px;
  left: -49px;
}

.modal__checkbox-input input:checked + label::before {
  content: "";
  display: block;
  position: absolute;
  width: 16px;
  height: 16px;
  background: url("../img/check.svg") no-repeat;
  background-position: center;
  top: 1px;
  left: -49px;
}

.modal__checkbox-input input {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.modal--show {
  display: block;
}

.modal__input--notvalid input {
  border: 1px solid #ff0000;
}

.modal__checkbox--notvalid {
  text-decoration: underline;
  text-decoration-color: #ff0000;
}